.tiles{
  ul{
    font-size: 0;
    text-align: center;
    li{
      display: inline-block;
      padding: $padding;
    }
  }
}
.tile{
  background: $grey1;
  display: flex;
  align-items: center;
  justify-content:center;
  padding-bottom: 60%;
  overflow: hidden;
  position: relative;
  margin-bottom: $padding*2;
  &:hover{
    figure{
      transform: scale(1.1);
      filter: grayscale(0%) contrast(100%);
      opacity:0.9;
    }
  }
  &.featured{
    float: none;
  }
  &:not(.featured){
    float: left;
    width:50%;
    //margin-right:2%;
    margin-bottom: 14px;
    padding-bottom: 30%;
   /*  &:nth-child(even){
      margin-right:0;
    } */
    .tile-title{
      color: $white;
      @include font-size(2);
    }
  }
  figure{
    background-size: cover;
    opacity:0.7;
    transition: all 4s cubic-bezier(.22,.61,.36,1);;
    position: absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    z-index: 0;
    filter: grayscale(100%) contrast(120%);
  }
  .copy{
    position: absolute;
    left:0;
    text-align: center;
    top:50%;
    width: 100%;
    transform: translateY(-50%);
    z-index: 3;
    span{
      color: $color-primary;
      font-weight: $font-weight-normal;
    }
  }
  .tile-title{
    color: $white;
    @include font-size(4);
  }
}